.modal {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  background-color: rgba(255, 184, 202, .4);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.is-hidden {
  visibility: hidden;
  transform: scale(1.2);
}

.close-btn {
  color: #2e2f42;
  width: 24px;
  height: 24px;
  background-color: rgba(255, 184, 202, .4);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.close-icon {
  width: 15px;
  height: 15px;
  stroke: #000;
}

.modal-content {
  width: 500px;
  height: 600px;
  background-image: url("about-modal-bg.189b3d20.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin: 0 auto;
  padding: 16px;
  position: relative;
}

.modal p {
  color: red;
  text-indent: 5px;
  margin-bottom: 0;
}

.modal-svg-ice-cream {
  width: 25px;
  height: 25px;
  background-color: rgba(0, 0, 0, 0);
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.about-modal-div {
  flex-direction: row;
  justify-content: space-between;
  align-items: baseline;
  padding-top: 20px;
  display: flex;
}

